import * as Sentry from "@sentry/browser";
import {chrome} from "../polyfill.ts";

export function sentryInit(_options = {}) {
    console.debug('sentryInit. ENVIRONMENT: ', ENVIRONMENT);
    let params = {
        dsn: "https://708a324f1a7ec87a2658346fb4bb7586@o438337.ingest.sentry.io/4506062147551232",
        tracesSampleRate: 0.00025, // 0.05%
        sampleRate: 0.005, // 0.05%
        // Session Replay
        replaysSessionSampleRate: 0, // This sets the sample rate at 10%. You may want to change it to 100% while in development and then sample at a lower rate in production.
        replaysOnErrorSampleRate: 0,
        // This variable is set in Webpack itself (see webpack.DefinePlugin)
        enabled: ENVIRONMENT === 'production',
        release: chrome.runtime.getManifest().version,
        attachStacktrace: true,
        ignoreErrors: [ // https://docs.sentry.io/platforms/javascript/configuration/filtering/
            'Could not establish connection. Receiving end does not exist',
            'No tab with id:',
            'Failed to fetch',
            'Corruption: block checksum mismatch',
            'QuotaExceededError: Encountered full disk',            
            'Non-Error promise rejection captured with value: IO error',
            'FILE_ERROR_NO_SPACE',
            'FILE_ERROR_IO',
            'FILE_ERROR_FAILED',
            'FILE_ERROR_ACCESS_DENIED',
        ]
    };
    Sentry.init(params);
    Sentry.getCurrentScope().setLevel("error");
}

export function sentryToggle(enabled) {
    Sentry.getCurrentHub().getClient().getOptions().enabled = enabled;
}
