export const PageType = {
    NORMAL: "NORMAL",
    SYSTEM: "SYSTEM",
    EXTENSION: "EXTENSION",
    BROWSER_GUARD: "BROWSER_GUARD",
    BLANK_NEW_TAB: "BLANK_NEW_TAB",
    CHROME_WEBSTORE: "CHROME_WEBSTORE",
    //MALWAREBYTES: "MALWAREBYTES"
};
const SETTING_WITH_URL_PERMISSIONS = [SETTING_ADS, SETTING_MALWARE, SETTING_SCAMS];
const port = {reload: null};
const exclusionToSettingsMapper = {
    [EXCLUSION_ADS]: SETTING_ADS,
    [EXCLUSION_MALWARE]: SETTING_MALWARE,
    [EXCLUSION_SCAMS]: SETTING_SCAMS
};

//NOTE: Current bug in chrome makes listening for popup close events impossible
//  https://bugs.chromium.org/p/chromium/issues/detail?id=31262#c13
export const reloadOnClose = function() {
    if (!port.reload) {
        //When we switch a switch, start a port to background page
        port.reload = chrome.runtime.connect({
            name: RELOAD_ON_CLOSE_PORT,
        });
        console.debug("Began listening for a popup close event");
    }
};

export const showGlobalNotification = (innerHTML, type = CSS_NOTIFICATION_INFO , showDuration = 3000) => {
    const notification = document.getElementById(CSS_GLOBAL_NOTIFICATION);
    notification.classList.add(type);
    notification.innerHTML = innerHTML;
    notification.style.display = 'flex';
    setTimeout(() => {
        notification.style.display = 'none';
    }, showDuration);
};

const settingsGet = function(
    settingIdConstant,
    successCallback,
    optionalErrorCallback
) {
    if (!optionalErrorCallback) {
        optionalErrorCallback = (msg) => console.error(msg);
    }
    chrome.runtime.sendMessage(
        {type: MSG_SETTINGS_GET, payload: settingIdConstant},
        function(response) {
            if (!response || response.error) {
                optionalErrorCallback(
                    "Error calling settingsGet: " +
                        (response ? response.error : "No response received")
                );
            } else {
                successCallback(response.success);
            }
        }
    );
};

export const settingsGetAsync = (settingIdConstant) => new Promise((resolve, reject) => {
    chrome.runtime.sendMessage(
        {type: MSG_SETTINGS_GET, payload: settingIdConstant},
        function(response) {
            if (!response || response.error) {
                // eslint-disable-next-line prefer-promise-reject-errors
                console.debug('SETTINGS GET ASYNC: ERROR CALLING settingsGet');
                // eslint-disable-next-line prefer-promise-reject-errors
                reject(`Error calling settingsGet: ${(response ? response.error : "No response received")}`);
            } else {
                resolve(response.success);
            }
        }
    );
});

/**
 * This function is used to get the settings by exclusion
 * @param {string} exclusionConst - The exclusion constant
 * @returns {Promise} - Returns a promise that resolves to the settings value
 * 
*/
export const getSettingsByEclusion = async (exclusionConst) => {
    const settingIdConstant = exclusionToSettingsMapper[exclusionConst];
    if (!settingIdConstant) {
        console.error(`No setting found for exclusion: ${exclusionConst}`);
        return null;
    }
    return await settingsGetAsync(settingIdConstant);
}

export function featureFlagGetAsync(feature) {
    return new Promise((resolve, reject) => {
        chrome.runtime.sendMessage({
            type: MSG_QUERY_FEATURE_FLAG,
            payload: { feature } 
        }, (response) => {
            if (!response) {
                reject(new Error(`Error getting feature flag for ${feature}`));
            } else {
                resolve(response.isEnabled);
            }
        });
    });
}

export const settingsSetAsync = (settingsPayload) => new Promise((resolve, reject) => {
    //NOTE: We are not directly accessing settings as later we may want to encrypt them so content scripts cant
    //change tem // TODO: Can content scripts access out settings via our background script
    chrome.runtime.sendMessage(
        {type: MSG_SETTINGS_SET, payload: settingsPayload},
        function(response) {
            if (!response || response.error) {
                // eslint-disable-next-line prefer-promise-reject-errors
                reject(`Error calling settingsSet: ${response ? response.error : "No response received"}`);
            } else {
                //TODO: That same payload probably needs to be applied to malwarebytes.SETTINGS
                resolve(response.success);
            }
        }
    );
});

export const updateCheckboxDisabledState = function(targetCheckbox, isDisabled) {
    $(targetCheckbox).prop("disabled", isDisabled);
    updateCheckboxParentToState(targetCheckbox);
};

const linkCheckboxToKillswitch = async function(
    targetCheckbox,
    settingIdConstant
) {
    //NOTE: Consent and Killswitch checkboxes don't get disabled, so no need to check
    if (
        settingIdConstant !== SETTING_KILLSWITCH &&
        settingIdConstant !== SETTING_TELEMETRY_CONSENT &&
        settingIdConstant !== SETTING_NATIVE_MESSAGING &&
        targetCheckbox
    ) {
        const killSwitchState = await getKillswitchStateAsync();
        $(targetCheckbox).prop("disabled", killSwitchState === false);
    }
};

export const getKillswitchState = function(callback) {
    settingsGet(SETTING_KILLSWITCH, callback);
};

export const getKillswitchStateAsync = () => settingsGetAsync(SETTING_KILLSWITCH);

export const updateCheckboxParentToState = (targetCheckbox) => {
    let parent = $(targetCheckbox).parent();
    if (targetCheckbox.checked) {
        parent.addClass("checked");
    } else {
        parent.removeClass("checked");
    }
    if ($(targetCheckbox).prop("disabled") === true) {
        parent.addClass("disabled");
    } else {
        parent.removeClass("disabled");
    }
};


/**
 * This function checks if the URL permission is removed and skips the event if it is
 * @param {string} settingIdConstant - The setting ID constant
 * @returns {promise} - Returns a promise that resolves to a boolean value indicating if the event was skipped
 * */
export const isFFUrlPermissionRemovedSkip = async (settingIdConstant = null) => {

    // If the setting is not in the list of settings that require URL permissions, return false
    if (settingIdConstant && !SETTING_WITH_URL_PERMISSIONS.includes(settingIdConstant)) {
        return false;
    }

    const UrlPermissionFlag = await simpleStorageGet(IS_URL_PERM_REMOVED_FLAG);
    if (UrlPermissionFlag) {
        return true;
    }

    return false
}

const bindSettingsCheckboxEvent = async function(
    targetCheckbox,
    settingIdConstant,
    optAfterChangeListener
) {
    targetCheckbox.addEventListener("change", async (event) => {
        
        const isProtectionOnHold = browserName() == BROWSERS.FIREFOX && 
        await isFFUrlPermissionRemovedSkip(settingIdConstant)

        if (isProtectionOnHold) {
            event.preventDefault();
            event.target.checked = false;
            showGlobalNotification(`
                <span class="pseudo-xmark"></span>${chrome.i18n.getMessage('URLPermissionRequired')}
            `, CSS_NOTIFICATION_WARNING);
            return;
        }

        //NOTE: Disable any clicked checkbox temporarily, to avoid spamming
        let defaultState = $(targetCheckbox).prop("disabled");
        console.debug(
            "CB: Default state of " +
                targetCheckbox.id +
                " is " +
                (defaultState ? "disabled" : "active")
        );
        $(targetCheckbox).prop("disabled", true); // don't allow changes while we sync
        let settings = {};
        settings[settingIdConstant] = $(targetCheckbox).prop("checked");
        updateCheckboxParentToState(targetCheckbox);

        if (
            settingIdConstant === SETTING_NATIVE_MESSAGING
        ) {
            if (settings[settingIdConstant]) {
                settings[settingIdConstant] = await msgPromptNativeMsg();
            } else {
                settings[settingIdConstant] = await msgRemoveNativeMessaging();
            }
        }
        //Excluding the killswitch checkbox action
        settingIdConstant !== SETTING_KILLSWITCH && await settingsSetAsync(settings);

        console.debug(
            "CB: Setting has been stored for " + targetCheckbox.id
        );
        if (optAfterChangeListener) {
            optAfterChangeListener(event);
        }

        setTimeout(() => {
            console.debug(
                "CB: Restoring " +
                    targetCheckbox.id +
                    " to " +
                    (defaultState ? "disabled" : "active")
            );
            updateCheckboxDisabledState(targetCheckbox, defaultState);
        }, 750);

        settingIdConstant !== SETTING_KILLSWITCH && reloadOnClose();
    });
};

const validatePageForInjection = (tab) => {
    return new Promise((resolve, _ignored) => {
        let validation = {
            id: tab.id,
            url: tab.url,
            isValid: true,
            pageType: PageType.NORMAL,
            host: tab.url, //updated outside of this method
            blocked: {totals: {}}, //counts, updated outside of this method
            redirectType: null, //eg malware, if we are a browserguard error page
            redirectSubType: null, //eg trojan, if we are a browserguard error page
        };
        if (validation.url.startsWith("chrome://")) {
            //"chrome://extensions/"
            validation.isValid = false;
            if (validation.url.startsWith("chrome://newtab/" || validation.url.startsWith("chrome://new-tab-page/"))) {
                validation.pageType = PageType.BLANK_NEW_TAB;
            } else {
                validation.pageType = PageType.SYSTEM;
            }
        } else if (
            validation.url.startsWith("chrome-extension://") ||
            validation.url.startsWith("moz-extension://") ||
            validation.url.startsWith("extension://")
        ) {
            //moz-extension://d013010e-febc-47bd-bf00-7bea93101aa7/
            //"chrome-extension://jlehollbgihdnbjpbdliihlmjmkifpcf/redirects/blockedMalware.html
            //          ?url=http%3A%2F%2Fscreensavers.com%2F&host=screensavers.com&type=malware&subtype=trojan"
            validation.isValid = false;
            if (
                validation.url.startsWith(`chrome-extension://${chrome.runtime.id}/`) ||
                (window.browser &&
                    validation.url.startsWith(
                        `moz-extension://${chrome.i18n.getMessage("@@extension_id")}/`
                    )
                ) ||
                validation.url.startsWith(`extension://${chrome.runtime.id}/`)
            ) {
                //look for browser guard's install id (the jlehollbgihdnbjpbdliihlmjmkifpcf above)
                validation.pageType = PageType.BROWSER_GUARD;
                let params = new URL(tab.url).searchParams;
                if (params.has("url")) {
                    validation.url = params.get("url");
                }
                if (params.has("host")) {
                    validation.host = params.get("host");
                }
                if (params.has("type")) {
                    validation.redirectType = params.get("type");
                }
                if (params.has("subtype")) {
                    validation.redirectSubType = params.get("subtype");
                }
            } else {
                validation.pageType = PageType.EXTENSION;
            }
        //NOTE: about: are mozilla settings pages
        } else if (validation.url.startsWith("about:")) {
            validation.isValid = false;
            if (["about:newtab", "about:blank"].some((page) => validation.url.startsWith(page))) {
                validation.pageType = PageType.BLANK_NEW_TAB;
            } else {
                validation.pageType = PageType.SYSTEM;
            }
        } else if (validation.url.startsWith("edge://")) {
            validation.isValid = false;
            if (validation.url.startsWith("edge://newtab/")) {
                validation.pageType = PageType.BLANK_NEW_TAB;
            } else {
                validation.pageType = PageType.SYSTEM;
            }
        } else if (validation.url.indexOf("chromewebstore.google.com") >= 0) {
            //This appears to be a protected store that does not return it's requests to us in background
            //that may-or-may-not be only as a consequence of opening via settings
            validation.isValid = false;
            validation.pageType = PageType.CHROME_WEBSTORE;
        }
        resolve(validation);
    });
};

