export const linkCheckboxToSetting = async function(
    checkboxId,
    settingIdConstant,
    optionalLoaderSelector,
    optAfterChangeListener
) {
    if (optionalLoaderSelector) {
        $(optionalLoaderSelector).addClass("active");
    }
    let targetCheckbox = document.getElementById(checkboxId);
    if (!targetCheckbox) {
        return;
    }
    //NOTE: Get starting value of checkbox
    await linkCheckboxToKillswitch(targetCheckbox, settingIdConstant);
    const loadedState = await settingsGetAsync(settingIdConstant);
    // Malwarebytes.SETTING_NATIVE_MESSAGING needs to allow for an undefined value
    if (settingIdConstant !== SETTING_NATIVE_MESSAGING) {
        targetCheckbox.checked = loadedState !== false;
    } else {
        targetCheckbox.checked = loadedState;
    }
    updateCheckboxParentToState(targetCheckbox);
    //NOTE: the following handles disabled toggling during event
    if (optionalLoaderSelector) {
        $(optionalLoaderSelector).removeClass("active");
    }
    await bindSettingsCheckboxEvent(
        targetCheckbox,
        settingIdConstant,
        optAfterChangeListener
    );
};

