export const isPremium = () => {
    return new Promise((res, rej) => {
        chrome.runtime.sendMessage({type: MSG_IS_PREMIUM}, function(response) {
            if (!response || response.error) {
                console.error("Failed to check if premium: " + response.error);
                rej(response.error);
            } else {
                console.debug("Is premium: " + response.success);
                res(response.success);
            }
        });
    });
};

