export const getCurrentTabData = (successCallback, optionalErrorCallback) => {
    if (!optionalErrorCallback) {
        optionalErrorCallback = (msg) => console.error(msg);
    }
    new Promise((resolve) => {
        chrome.tabs.query(
            {currentWindow: true, active: true},
            function(tabs) {
                resolve(tabs);
            }
        );
    })
        .then((tabs) => {
            return validatePageForInjection(tabs[0]);
        })
        .then((validationData) => {
            if (validationData.isValid) {
                chrome.runtime.sendMessage(
                    {
                        type: MSG_TAB_DATA_GET,
                        payload: validationData.id,
                    },
                    function(response) {
                        if (!response || response.error) {
                            //RECOVER GRACEFULLY
                            successCallback(validationData);
                            console.error(
                                "Error calling getCurrentTabData: " +
                                (response ?
                                    response.error :
                                    "No response received")
                            );
                        } else {
                            validationData.blocked = response.success.blocked;
                            validationData.host = response.success.host;
                            validationData.excluded = response.success.excluded;
                            successCallback(validationData);
                        }
                    }
                );
            } else {
                successCallback(validationData);
            }
        })
        .catch((err) => {
            optionalErrorCallback(err);
        });
};
export const ellipsisSlice = (text, maxLength = 22)=> {
    return `${text.slice(0, maxLength)}...`;
};

export function downloadJSONfile(data, filename) {
    const blob = new Blob([JSON.stringify(data)], { type: 'application/json' });
    const url = URL.createObjectURL(blob);
    chrome.downloads.download({
        url,
        filename,
        saveAs: true
    });
}


export const closePopup = () => {
    // send message to background to close the popup with message MSG_CLOSE_POPUP and the call back from there returns true of false
    chrome.runtime.sendMessage({type: MSG_CLOSE_POPUP}, function(response) {
        if (response) {
            console.log('Popup closed successfully');
        } else {
            throw new Error('Failed to close the popup');   
        }
    });
};


/*
* This function requests the URL permissions from the user
* permissionsToRequest type :{ origins?: string[] | undefined, permissions?: string[] | undefined }
*/
export async function requestAccessPermissions(permissionsToRequest) {
    
    const onResponse = async (response) => {
        if (response) {
            console.log("Permission was granted", await chrome.permissions.getAll());
            return true;
        } else {
            console.log("Permission was refused");
            return false;
        }
    }

    const response = await chrome.permissions.request(permissionsToRequest);
    const result = await onResponse(response);
    console.log(`Permissions request operation result:`, result);

    return result;    
}

/*
* This function removes the URL permissions from the user
* permissionsToRemove type :{ origins?: string[] | undefined, permissions?: string[] | undefined }
* @returns {boolean} - Returns a boolean value indicating if the permission was removed
*/
export async function removeAccessPermissions(permissionsToRemove) {
    const onResponse = async (response) => {
        if (response) {
            console.log("Permission was removed", await chrome.permissions.getAll());
            return true;
        } else {
            console.log("Permission was not removed");
            return false;
        }
    }

    const response = await chrome.permissions.remove(permissionsToRemove);
    const result = await onResponse(response);
    console.log(`Permissions remove operation result:`, result);

    return result;
}

/*
* Handle the permission request button click event in URL Permission Bar 
*/

export const handleFFPermissionRequestBtn = async ()=> {
    const permissionsToRequest = { origins: DEFAULT_PERMISSIONS.origins };
    if ( await requestAccessPermissions(permissionsToRequest) ) {
        chrome.runtime.sendMessage({
            type: MSG_UPDATE_PROTECTION_ON_PERM_CHANGE,
            payload: { status: true }
        })
        chrome.runtime.sendMessage({
            type: MSG_MV3_TOGGLE_INDIVIDUAL_PROTECTION,
            protection: MV3_PROTECTION_ADS,
            enable: true
        });
        chrome.runtime.sendMessage({
            type: MSG_MV3_TOGGLE_INDIVIDUAL_PROTECTION,
            protection: MV3_PROTECTION_MALWARE,
            enable: true
        });
        chrome.runtime.sendMessage({
            type: MSG_MV3_TOGGLE_INDIVIDUAL_PROTECTION,
            protection: MV3_PROTECTION_SCAMS,
            enable: true
        });
    }
};

/*
* Show permission request in document by setting display: flex
*/
export const showFFPermsRequestBarByFlag = async (document) => {
    const permissionFlag = await simpleStorageGet(IS_URL_PERM_REMOVED_FLAG);
    if (permissionFlag) { // by default, display: none

        document.querySelector('.url-permission-bar').style.display = 'flex';

        translateText([
            {id: "permission-req-bar-desc", msg: "SettingsPermissionReqBarDesc"},
            {id: "permission-req-bar-btn", msg: "SettingsPermissionReqBarBtn"},
        ], document);

        document.querySelector(`.url-permission-bar #permission-req-bar-btn`).addEventListener(
            'click', handleFFPermissionRequestBtn
        );
    }
}

export const getUngrantedPermissions = async () => {

    const browser = browserName();
    const wantedPermissions = BROWSER_PERMISSIONS[browser]?.permissions || [];
    const wantedOrigins = BROWSER_PERMISSIONS[browser]?.origins || [];
    const allWantedPermissions = [...wantedPermissions, ...wantedOrigins];

    const currentPermissions = await chrome?.permissions?.getAll();

    const ungrantedPermissions = allWantedPermissions.filter(permission => {
        return !currentPermissions?.permissions?.includes(permission);
    });

    return ungrantedPermissions || [];
};

export async function  showOptionalPermRequestBar(document) {
    const ungrantedPermissions = await getUngrantedPermissions();
    if (ungrantedPermissions.length > 0) {
        document.querySelector('.url-permission-bar').style.display = 'flex';

        translateText([
            { id: "permission-req-bar-desc", msg: "OptionalPermissionReqBarDesc" },
            { id: "permission-req-bar-btn", msg: "OptionalPermissionReqBarBtn" },
        ], document);

        document.querySelector(`.url-permission-bar #permission-req-bar-btn`).addEventListener(
            'click', async () => {
                const url = chrome.runtime.getURL("app/eventpages/permission-request.html");
                await chrome.tabs.create({ url });
            }
        );
    } else { // No ungranted permissions, hide the bar
        document.querySelector('.url-permission-bar').style.display = 'none';
    }
}

/*
* This function checks if the extension is pinned to the toolbar
*/
export async function isExtensionPinnedOnToolbar() {
    try {
        const userSettings = await chrome.action.getUserSettings();
        // If isOnToolbar is defined and true, return true; otherwise, return false
        return Boolean(userSettings?.isOnToolbar);
    } catch (error) {
        console.error('Failed to get user settings (extension pinned):', error);
        return false;
    }
}


