const setupUpgradeCardByPremiumState = async () => {
    console.log('Browser: ', TARGET_BROWSER);
    const isPremiumUser = await isPremium();
    console.log('Premium status: ', isPremiumUser);
    if (!isPremiumUser) {
        document.getElementById('upgrade-protection').style.display = 'inherit';
    } else {
        document.getElementById('upgrade-protection').style.display = 'none';
    }
}

const showUpgradeCard = async () => {
    await setupUpgradeCardByPremiumState();
    if (TARGET_BROWSER === BROWSERS.FIREFOX) {
        // Firefox takes longer to talk with NH and set premium status -dirty fix- , wait then re-run
        document.getElementById('upgrade-protection').style.display = 'none';
        setTimeout(async () => {
            await setupUpgradeCardByPremiumState();
        }, 20000);// 20 seconds should be enough for the NH to start the connection and set the premium status
    }
};

const checkExtensionOnToolbar = async () => {
    const isPinned = await isExtensionPinnedOnToolbar()
    if (isPinned){
        // remove element not-pinned-hide, already-pinned-show stays
        document.getElementById('not-pinned-hide').remove();
        document.getElementById('already-pinned-show').style.display = 'block';
        translateText(bgPinnedNodes);
    } else {
        // remove element already-pinned-show, not-pinned-hide stays
        document.getElementById('already-pinned-show').remove();
        document.getElementById('not-pinned-hide').style.display = 'block';
    }
};

document.addEventListener("DOMContentLoaded", () => {
    checkExtensionOnToolbar();
    showUpgradeCard();
    translateText(nodes);
});
